.corner-text{margin:0.5rem 0 0 0.5rem;font-size:0.16rem}

.corner-mouse{width:0.21rem;height:0.77rem;position:absolute;left:0.5rem;bottom:0.5rem;background:url(data:image/png;base64,) no-repeat;background-size:100%}

.corner-mouse:after{content:"";display:block;width:0.04rem;height:0.04rem;margin:0 auto;margin-top:0.07rem;border-radius:1rem;background-color:#fff;-webkit-animation:1s slideDownward infinite linear;animation:1s slideDownward infinite linear}

.corner-right{width:0.03rem;height:20%;border-radius:1rem;background-image:-webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0)), to(rgba(255,255,255,0.3)));background-image:linear-gradient(to bottom, rgba(255,255,255,0), rgba(255,255,255,0.3));position:absolute;right:0.5rem;bottom:0.5rem}

.main-text{position:absolute;top:50%;left:50%;font-size:1rem;white-space:nowrap;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}

.main-text:before{content:"";display:block;position:absolute;left:-1.2rem;bottom:-0.5rem;width:4rem;height:4rem;pointer-events:none;background:url(data:image/png;base64,) no-repeat}

.main-text strong{color:#faad14;font-size:0.6rem;font-weight:inherit;letter-spacing:-12px}

.main-text .author-wrap .text{display:inline-block;font-size:0.24rem;letter-spacing:-5px;margin-top:1.5rem}

.main-text .author-wrap .text:after{content:"";display:block;margin-top:0.15rem;width:100%;height:3px;border-radius:1rem;background-image:-webkit-gradient(linear, left top, right top, from(#faad14), to(rgba(250,173,20,0)));background-image:linear-gradient(to right, #faad14, rgba(250,173,20,0))}

.main-text .author-wrap .website{font-size:0.4rem;margin-top:0.1rem}

.main-text .author-wrap .website a{display:block;color:#ffffff;margin-right:0.1rem;-webkit-transition:all 0.5s;transition:all 0.5s}

.main-text .author-wrap .website a:hover{-webkit-transform:translateY(6px);transform:translateY(6px)}

.main-text .author-wrap .website a:nth-child(1):hover{color:#ffe300}

.main-text .author-wrap .website a:nth-child(2):hover{color:#3d7eff}

.main-text .author-wrap .website a:nth-child(3):hover{color:#f082ac}
